    background: @gray-10;
    color: @gray;
    .h5();
    margin-bottom: 0;
}

.expandable-group .expandable {
    border-bottom: 1px solid @gray-80;
    margin-bottom: 0;
    background: @white;
}

.expandable-group .expandable_label {
    .h4();
